import React from 'react';
import { NavLink } from 'react-router-dom';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

export default (props) => { return props.onClick === false ? (
	<NavLink to={props.to} className="float-button" title={props.title || "Adicionar"}><FontAwesomeIcon icon={props.icon || "plus"} /></NavLink>
	) : ( 
	<button className="float-button" onClick={props.onClick} title={props.title || "Adicionar"}><FontAwesomeIcon icon={props.icon || "plus"} /></button>
	)
};