export const formatDecimal = (value, cb=false) => { 
    // value = (value >= 1) ? value.toFixed(2) : value;
    let newValue = String(value).padStart(4, '0').replace(/\D/g, '').replace(/(\d+)(\d{2})/, '$1.$2') || 0;
    if (cb) {
        cb(newValue);
    } 
    return Number(newValue).toFixed(2);
};